.menu{
    margin: 0;
    padding: 0;
    position: fixed;
    top: 0;
    left: -100%;
    width: 600px;
    height: 100%;
    background: #FFF;
    z-index: 1;
    transition: 0.5s;
}
ul{
    margin: 0;
    padding: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}
ul li{
    list-style: none;
}

ul li a{
    padding: 10px;
    display: inline-block;
    font-family: verdana;
    font-size: 2em;
    text-decoration: none;
    text-transform: uppercase;
    color: #262626;
}
ul li a:hover{
    background: #FFFC85
}
.menu.active{
    left: 0;
    overflow: auto;
    z-index: 2;
}

.toggle{
    position: fixed;
    top: 40px;
    left: 40px;
    z-index: 3;
}
.toggle button{
    text-decoration: none;
    color: #262626;
    font-size: 24px;
    cursor: pointer;
}
.btn_menu{
    appearance: none;
    border: 0;
    border-radius: 5px;
    background: #00bfff;
    color: #fff;
    padding: 8px 8px;
    font-size: 16px;
    width: 100%;

}
.navigate {
    margin-top: 20px;
}