.error{
    width: 100%;
    text-align: center;
    animation: gradient 2.4s infinite ease-in-out;
    letter-spacing: 10px;
    white-space: nowrap;
    font-size: 50px;
    text-indent: 10px;
}


.s1{
    font-size: 24px
}
.s2{
    font-size: 28px
}
.s3{
    font-size: 32px
}
.s4{
    font-size: 36px
}
.s4{
    font-size: 40px
}
.s5{
    font-size: 44px
}
.s6{
    font-size: 48px
}



.a01{
    animation-delay: 0.1s;
}
.a02{
    animation-delay: 0.2s;
}

.a03{
    animation-delay: 0.3s;
}

.a04{
    animation-delay: 0.4s;
}

.a05{
    animation-delay: 0.5s;
}

.a06{
    animation-delay: 0.6s;
}

/*post styles*/


.post__div{
    width: 550px;
    height: 220px;
    border: 5px solid #F0F8FF;
    margin: 7px 0;
}
.post__h3{
    margin: 10px;
    padding: 0;
}

.post__p {
    margin: 0px 15px;
    padding: 12px 0px;
    text-align: left;
    border-bottom: 3px solid #61dafb;
}





@keyframes gradient{
    0%{
        letter-spacing: 10px;
        text-indent: 10px;
    }
    40%{
        letter-spacing: 50px;
        text-indent: 50px;
    }

    80%{
        letter-spacing: 10px;
        text-indent: 10px;
    }


    100%{
        letter-spacing: 10px;
        text-indent: 10px;
    }

}